==============================*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  transition: 0.3s; }
  .header__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 4.8rem;
    position: relative; }
    .header__content:before {
      content: '';
      position: absolute;
      display: block;
      top: 100%;
      left: 50%;
      width: 100%;
      max-width: 50rem;
      height: 2px;
      background-color: white;
      opacity: 0.5;
      transform: translateX(-50%); }
  .header__logo {
    position: relative;
    height: 4rem;
    display: block;
    cursor: pointer; }
    .header__logo object {
      width: 10rem;
      transition: 0.3s; }
    .header__logo object:last-child {
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0; }
  .header__nav {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 0;
    background-color: white;
    right: 0;
    transform: translate3d(700px, 0, 0);
    transition: 0.5s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1; }
    .header__nav a {
      font-size: 0.8rem;
      color: #0f1423;
      margin-bottom: 2rem;
      font-family: inherit; }
    .header__nav a:last-child {
      margin-bottom: 0; }
    .header__nav--active {
      width: 100%;
      transform: translate3d(0, 0, 0); }
  .header__social {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
    border-top: 2px solid black;
    padding: 2rem 1rem 0;
    margin-top: 2rem; }
    .header__social a {
      margin: 0 5px;
      font-size: 1rem; }
  .header__btn {
    max-width: 10rem;
    color: rgba(255, 255, 255, 0.95);
    font-size: 0.8rem;
    font-weight: 600;
    border-radius: 3px;
    text-align: center;
    background-color: #0b9ff5;
    background-image: none;
    margin-right: 4rem;
    padding: .75rem 1.25rem;
    transition: all .2s;
    position: relative; }
    .header__btn:hover {
      color: white; }
    .header__btn::after {
      content: '';
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: all .2s; }
    .header__btn:hover::after {
      outline: #0b9ff5;
      box-shadow: 0 0 2rem 5px rgba(11, 159, 245, 0.2); }
  .header__menu {
    position: absolute;
    width: 2.9rem;
    height: 1.8rem;
    display: block;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 2; }
    .header__menu span {
      display: block;
      position: absolute;
      width: 100%;
      height: .2rem;
      left: 0;
      background-color: black;
      transition: 0.3s;
      opacity: 1; }
    .header__menu span:first-child {
      top: 0; }
    .header__menu span:nth-child(2) {
      top: 50%;
      margin-top: -1px; }
    .header__menu span:last-child {
      top: 100%;
      margin-top: -2px; }
    .header__menu--active span {
      background-color: #0f1423; }
    .header__menu--active span:first-child {
      -webkit-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      transform: rotate(-45deg);
      top: 50%;
      margin-top: -2px; }
    .header__menu--active span:nth-child(2) {
      opacity: 0; }
    .header__menu--active span:last-child {
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      transform: rotate(45deg);
      top: 50%; }
  .header--active, .header--white {
    background-color: white;
    background-image: linear-gradient(90deg, #171f2d 0%, #03030a 100%);
    background-image: none;
    -webkit-box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.08);
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.08); }

.header--active .header__nav a,
.header--white .header__nav a {
  color: black; }

.header--active .header__menu span,
.header--white .header__menu span {
  background-color: black; }

.header--active .header__content:before,
.header--white .header__content:before {
